.accordion {

    .card-header {

        $icon-size: 28px;
        
        a {
            cursor: pointer;
            padding-right: $icon-size + 10px;
            text-decoration: none;
    
            .accordion-closed-icon, .accordion-open-icon {
                width: $icon-size;
                right: 0;
                bottom: 0;

                @supports (display: block) {
                    align-self: center;
                }
            }
    
            &.collapsed {
                .accordion-closed-icon {
                    display: flex;
                }
                .accordion-open-icon {
                    display: none;
                }
            }
    
            &:not(.collapsed) {
                .accordion-closed-icon {
                    display: none;
                }
                .accordion-open-icon {
                    display: flex;
                }
            }
        }
    }
    
}
