.snap-list {

    display: flex;
    scroll-snap-type: x mandatory;
    align-items: stretch;
    scroll-behavior: smooth;
    overflow-x: hidden;

    @supports (overflow-x: auto) {
        overflow-x: auto;
    }

    @media (hover: hover) {
        overflow-x: hidden;
    }

    .snap-item {
        scroll-snap-align: start;
        flex-shrink: 0;
        flex-grow: 1;
        flex-basis: 95%;

        @include media-breakpoint-up(sm) {
            flex-basis: 50%;
        }
        @include media-breakpoint-up(lg) {
            flex-basis: 33.333%;
        }
        @include media-breakpoint-up(xl) {
            flex-basis: 25%;
        }

        .card {
            height: 100%;
            max-width: 500px;
            margin: 0 auto;
        }
    }
}
