.rule {
    $knob: 0.85em;
    border-bottom: 2px solid currentColor;
    height: 0;
    margin-right: $knob;
    position: relative;

    &::after {
        content: " ";
        border: 3px solid currentColor;
        border-radius: 50%;
        display: inline-block;
        position: absolute;
        width: $knob;
        height: $knob;
        left: 100%;
        top: calc(50% + 1px);
        transform: translateY(-50%);

    }
}
