@font-face {
	font-family:'Messina';
	src: url('../static/fonts/MessinaSansWeb-Black.eot'); /* IE9 Compat Modes */
	src: url('../static/fonts/MessinaSansWeb-Black.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
	     url('../static/fonts/MessinaSansWeb-Black.woff2') format('woff2'), /* Super Modern Browsers */
         url('../static/fonts/MessinaSansWeb-Black.woff') format('woff'); /* Pretty Modern Browsers */
	font-weight: 900;
	font-style: normal;
	font-stretch: normal;
	unicode-range: U+0000-024F;
}

