.skip-nav {
    position: absolute;
    left: 0.5rem;
    top: -3.5rem;
    transition-property: opacity, left, top;
    transition-duration: 0.1s;
    transition-timing-function: ease-in-out;

    &:focus {
        left: 0.5rem;
        top: 0.5rem;
        opacity: 1;
        z-index: $zindex-modal;
    }
}
