// A "nest" is a 1-up with a background illustration
.nest {

    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    height: 600px;
    justify-content: center;
    align-items: center;

    .nest-content {
        background-color: rgba(yellow, 0.5);
        text-align: center;
        max-width: 320px;
        padding: 1rem;
    }
}
