@if $input-underline {

    $input-border-focus-delta: 1px; // extra amount to expand input border on focus

    input.form-control, select.custom-select {
        border-style: none none solid;
        border-width: $input-border-width;

        &:focus {
            border-width: $input-border-width + $input-border-focus-delta;
        }
    }

    .input-group {

        > .form-control,
        > .form-control-plaintext,
        > .custom-select,
        > .custom-file {
            align-self: flex-end;
        }

        input.form-control {
            padding: $input-group-padding-y $input-group-padding-x;

            &:first-child {
                padding-left: $input-padding-x;
            }

            &:last-child {
                padding-right: $input-padding-x;
            }
        }

    }

    .input-group-append {
        .ghost-btn {
            border-radius: 0;
            border-color: $input-border-color;
            border-width: $input-border-width;
            border-style: none none solid;
            padding: 0 0.25rem;
            opacity: 1;
        }
    }

    .form-control:focus + .input-group-append {
        .ghost-btn {
            border-color: $input-focus-border-color;
            border-width: $input-border-width + $input-border-focus-delta;
        }
    }

    .text-white {
        .input-group-append {
            .ghost-btn {
                color: $white;
                border-color: $input-border-color-light;

                &:focus, &:not(:disabled):not(.disabled):active:focus {
                    box-shadow: 0 0 0 $input-btn-focus-width rgba($secondary, 0.75);
                }
            }
        }
    }

}

.text-white {
    .form-control {
        color: $white;
        border-color: $input-border-color-light;

        &:focus {
            border-color: $secondary;
        }
    }
}

.text-white.ghost-btn, .text-white .ghost-btn {

    &:focus, &:not(:disabled):not(.disabled):active:focus {
        box-shadow: 0 0 0 $input-btn-focus-width rgba($secondary, 0.75);
    }
}

.ghost-btn:not(:disabled):not(.disabled):active, .ghost-btn:not(:disabled):not(.disabled).active {
    box-shadow: none;
}

.ghost-btn:not(:disabled):not(.disabled):active:focus, .ghost-btn:not(:disabled):not(.disabled).active:focus {
    box-shadow: none;
}

.form-control:disabled, .custom-select:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.input-group-text {
    padding: $input-group-padding-y $input-group-padding-x;
}

.input-group-sm {
    > .input-group-prepend,
    > .input-group-append {
        > .input-group-text {
            padding: $input-group-padding-y-sm $input-group-padding-x-sm;
        }
    }
}

.input-group-lg {
    > .input-group-prepend,
    > .input-group-append {
        > .input-group-text {
            padding: $input-group-padding-y-lg $input-group-padding-x-lg;
        }
    }
}

.form-group {
    position: relative;

    label {
        color: $text-muted;
        font-size: $small-font-size;
    }

    .form-hint {
        position: absolute;
        right: -5px;
        top: 1.75rem;
        bottom: 0.5rem;
        width: 1em;
        height: 1em;
        background: no-repeat url("data:image/svg+xml;charset=utf8,%3Csvg width='16' height='16' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='black' fill-rule='evenodd'%3E%3Cpath d='M8 14.5a6.5 6.5 0 100-13 6.5 6.5 0 000 13zM8 16A8 8 0 118 0a8 8 0 010 16z'/%3E%3Cpath d='M9 13H7V7h2z'/%3E%3Ccircle cx='8' cy='4' r='1'/%3E%3C/g%3E%3C/svg%3E");
        background-size: contain;
        overflow: hidden;
        color: rgba(0, 0, 0, 0);
    }

}


.form-group-checkbox {
    position: relative;

    .form-hint {
        position: absolute;
        margin-left: 3px;
        width: 1em;
        height: 1em;
        background: no-repeat url("data:image/svg+xml;charset=utf8,%3Csvg width='16' height='16' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='black' fill-rule='evenodd'%3E%3Cpath d='M8 14.5a6.5 6.5 0 100-13 6.5 6.5 0 000 13zM8 16A8 8 0 118 0a8 8 0 010 16z'/%3E%3Cpath d='M9 13H7V7h2z'/%3E%3Ccircle cx='8' cy='4' r='1'/%3E%3C/g%3E%3C/svg%3E");
        background-size: contain;
        overflow: hidden;
        color: rgba(0, 0, 0, 0);
    }

    .custom-control-input
    {
        overflow-wrap: anywhere;
    }

}


label.form-envoy {

    $font-size: 1rem;
    $font-size-active: 0.8rem;

    color: $input-color;
    font-size: $font-size;
    height: 1.5rem;
    margin-bottom: 0;
    position: relative;
    top: 1.5rem;
    transition-duration: 0.2s;
    transition-timing-function: ease;
    transition-property: margin-bottom, top, font-size, color;
    z-index: 1;
    pointer-events: none;

    &.active {
        color: $text-muted;
        font-size: $font-size-active;
        top: 0;
        margin-bottom: -($font-size - $font-size-active);
    }

    &.content-selector {
        color: $text-muted;
        font-size: $font-size-active;
        top: 0;
        margin-bottom: -($font-size - $font-size-active);
    }

    &.disabled {
        color: $text-muted;
    }

}

.text-white {
    label.form-envoy {
        color: $white;
    }
}

@include media-breakpoint-up(md) {
    .form-group.form-group-lg {
        .form-control {
            font-size: $input-font-size-lg;
        }
    }
}

.custom-select {
    background-image: url('../static/icons/caret-down.svg#asset');
}

.custom-control-input {
    &:focus ~ .custom-control-label::before {
        border-color: $input-focus-border-color;
        box-shadow: 0 0 0 $input-btn-focus-width rgba($secondary, 0.25);
    }
}

// Search input clear button removal
.form-control[type="search"] {

    &::-webkit-search-cancel-button {
        display: none;
    }

    &::-ms-clear {
        display: none;
    }
}

// Ghost back button

.ghost-btn-back {
    background-image: url("../static/images/arrows/left-black.svg");
    background-repeat: no-repeat;
    background-position: center left;
    background-size: 1.5rem;
    padding-left: 2.0rem;
}

.text-white.ghost-btn-back, .text-white .ghost-btn-back {
    background-image: url("../static/images/arrows/left-white.svg");
}

// Ghost submit button

.ghost-btn-submit {
    background-image: url("../static/images/arrows/right-long-black.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 2.5rem;
    width: 3rem;
}

.text-white.ghost-btn-submit, .text-white .ghost-btn-submit {
    background-image: url("../static/images/arrows/right-long-white.svg");
}
