tbody > tr.pagingDiv table {
    padding: 0px;
}
tbody > tr.pagingDiv table td {
    display: inline;
    position: relative;
    margin-left: auto;
    padding: 8px;
    border: 0px solid transparent;
    background:white;
}
.pagingDiv a {
    padding: 0.3rem 0.6rem;
    line-height: 0.4;
    color:#002b49 !important;
    font-weight: 700;
    border-radius: 40% !important;
    background:white;
}
.pagingDiv span {
    padding: 0.3rem 0.6rem;
    line-height: 0.4;
    color: white;
    background-color: #002b49;
    font-weight: 700;
    border-radius: 40% !important;
    background:#002b49;
}
.pagingDiv a:hover {
    border-radius: 40% !important;
    border: 1px;
    padding: 0.3rem 0.6rem;
    line-height: 0.4;
    border-color:azure;
}
.pagingDiv td{
    background-color: white;
}
