.animation-group {

    width: 100%;
    height: 100%;

    display: inline-block;
    position: relative;
    transition: opacity 0.6s ease;
    opacity: 1;

    &.animation-pending {
        opacity: 0;
    }

    .animation-layer {
        position: absolute;
        top: 0;
        left: 0;
        transition-property: transform;
        transition-timing: cubic-bezier(0.075, 0.75, 0.5, 0.95);
        transition-duration: 1.0s;

        @for $i from 1 through 8 {
            &:nth-child(#{$i}) {
                transform: rotate(0) scale(1, 1) translate(0, 0);
            }
        }

    }

    &.bullseye1 {

        .animation-layer {

            &:nth-child(1), &:nth-child(2) {
                transition-duration: 1.4s;
            }

            &:nth-child(3), &:nth-child(4) {
                transition-duration: 1.2s;
            }

            &:nth-child(5), &:nth-child(6) {
                transition-duration: 1.0s;
            }

            &:nth-child(7), &:nth-child(8) {
                transition-duration: 0.8s;
            }
        }

        &.animation-pending {
            .animation-layer {

                &:nth-child(1) {
                    transform: rotate(25deg) scale(1.3, 1.3);
                }

                &:nth-child(2) {
                    transform: rotate(-25deg) scale(1.3, 1.3);
                }

                &:nth-child(3) {
                    transform: rotate(25deg) scale(1.3, 1.3);
                }

                &:nth-child(4) {
                    transform: rotate(-25deg) scale(1.3, 1.3);
                }

                &:nth-child(5) {
                    transform: rotate(15deg) scale(0.8, 0.8);
                }

                &:nth-child(6) {
                    transform: rotate(-15deg) scale(0.8, 0.8);
                }

                &:nth-child(7) {
                    transform: translate(10%, -10%);
                }

                &:nth-child(8) {
                    transform: translate(-10%, 10%);
                }

            }
        }
    }

    &.bullseye2 {

        &.animation-pending {
            .animation-layer {

                &:nth-child(1) {
                    transform: rotate(90deg);
                }

                &:nth-child(2) {
                    transform: rotate(80deg);
                }

                &:nth-child(3) {
                    transform: rotate(70deg);
                }

                &:nth-child(4) {
                    transform: rotate(60deg);
                }

                &:nth-child(5) {
                    transform: rotate(50deg);
                }

                &:nth-child(6) {
                    transform: rotate(40deg);
                }

                &:nth-child(7) {
                    transform: rotate(30deg);
                }

                &:nth-child(8) {
                    transform: rotate(20deg);
                }

            }
        }
    }

    &.bullseye3 {

        .animation-layer {

            &:nth-child(1), &:nth-child(2) {
                transition-duration: 1.4s;
            }

            &:nth-child(3), &:nth-child(4) {
                transition-duration: 1.2s;
            }

            &:nth-child(5), &:nth-child(6) {
                transition-duration: 1.0s;
            }

            &:nth-child(7), &:nth-child(8) {
                transition-duration: 0.8s;
            }
        }

        &.animation-pending {
            .animation-layer {

                &:nth-child(1) {
                    transform: rotate(45deg) translate(0%, 20%);
                }

                &:nth-child(2) {
                    transform: rotate(45deg) translate(14%, 14%);
                }

                &:nth-child(3) {
                    transform: rotate(45deg) translate(20%, 0%);
                }

                &:nth-child(4) {
                    transform: rotate(45deg) translate(-14%, 14%);
                }

                &:nth-child(5) {
                    transform: rotate(45deg) translate(-20%, 0%);
                }

                &:nth-child(6) {
                    transform: rotate(45deg) translate(-14%, -14%);
                }

                &:nth-child(7) {
                    transform: rotate(45deg) translate(0%, -20%);
                }

                &:nth-child(8) {
                    transform: rotate(45deg) translate(14%, -14%);
                }

            }
        }
    }

}
