$displays: none, block, flex, grid, inline, inline-block, inline-flex, inline-grid;
$sides: 'top' 'bottom' 'left' 'right';
$pcts: 0, 25, 50, 75, 100;

@each $side in $sides {

    .anchor-#{$side} {
        #{$side}: 0;
    }

    @each $pct in $pcts {
        .anchor-#{$side}-#{$pct} {
            #{$side}: #{$pct + '%'};
        }
    }

}

.anchor-x {
    @extend .anchor-left, .anchor-right;
}

.anchor-y {
    @extend .anchor-top, .anchor-bottom;
}

.anchor-center-x {
    left: 50%;
    transform: translateX(-50%);
}

.anchor-center-y {
    top: 50%;
    transform: translateY(-50%);
}

.bg-azure {
    background-color: $azure;
}

@each $display in $displays {
    @include support-new {
        .d-new-#{$display} {
            display: $display !important;
        }
    }
    @include support-edge {
        .d-edge-#{$display} {
            display: $display !important;
        }
    }
}

.bg-primary-grad {
    @for $n from 1 through 6 {
        &:nth-child(#{$n}) {
            background-color: theme-color-level('primary', 1-$n);
        }
    }
}
