.site-table {

    .site-table-scroll {
        overflow: auto;

        table {

            thead {
                
                th {
                    vertical-align: middle;
                }
    
                th:first-child {
                    border-right: 10px solid $primary;
                }
            }
    
            tbody {
    
                tr {
                    background-color: theme-color-level('primary', -1);
                }
    
                td {
                    vertical-align: middle;
                }
            }
    
            tr {
                border-bottom:  (5 * $table-border-width) solid $primary;
            }
        }
    }
}
