// special component spacing utilities

$component-vertical-space-xs: 3rem;
$component-vertical-space-md: 5rem;

.component-pt {
    padding-top: $component-vertical-space-xs !important;
    @include media-breakpoint-up(md) {
        padding-top: $component-vertical-space-md !important;
    }
}

.component-pb {
    padding-bottom: $component-vertical-space-xs !important;
    @include media-breakpoint-up(md) {
        padding-bottom: $component-vertical-space-md !important;
    }
}

.component-py {
    @extend .component-pt;
    @extend .component-pb;
}

// intermediate variations where padding collapses at very beginning or end of container

.component-pt-mid {
    @extend .component-pt;

    &:first-child {
        padding-top: 0 !important;
    }
}

.component-pb-mid {
    @extend .component-pb;

    &:last-child {
        padding-bottom: 0 !important;
    }
}

.component-py-mid {
    @extend .component-pt-mid;
    @extend .component-pb-mid;
}

.component-header {
    max-width: 30rem;
    margin: auto;
}

.component-body {
    max-width: 45rem;
    margin: auto;
}
