.fz {

    /*
    The fz heights must precisely match the aspect ratios of the background images.
     */
    $swoop-height: 36%;
    $swoop-center-height: 12.5%;
    $swoop-hot-margin-side: 36%;

    position: relative;
    overflow: hidden;

    // #### ALIGNMENTS ####

    &.fz-center {
        text-align: center;
    }

    // #### BACKGROUNDS ####

    .fz-bg {
        background-repeat: no-repeat;
        background-position: top;
        height: 0;
        overflow: hidden;
        width: 100%;
        background-size: cover;
    }

    &.fz-expanded {
        .fz-bg {
            padding-bottom: $swoop-height;
        }

        &.fz-center {
            .fz-bg {
                padding-bottom: $swoop-center-height;
            }
        }
    }

    &.fz-expanded {

        &.fz-center {
            .fz-bg {
                background-image: url("../static/images/swoops/swoop-center.svg");
            }

            &.fz-dark {
                .fz-bg {
                    background-image: url("../static/images/swoops/swoop-center-dark.svg");
                }
            }
        }

        &.fz-left {
            .fz-bg {
                background-image: url("../static/images/swoops/swoop-left.svg");
            }

            &.fz-dark {
                .fz-bg {
                    background-image: url("../static/images/swoops/swoop-left-dark.svg");
                }
            }
        }

        &.fz-right {
            .fz-bg {
                background-image: url("../static/images/swoops/swoop-right.svg");
            }

            &.fz-dark {
                .fz-bg {
                    background-image: url("../static/images/swoops/swoop-right-dark.svg");
                }
            }
        }

    }

    // #### FOREGROUNDS ####

    .fz-fg {
        display: flex;

        .fz-content {
            flex-basis: auto;
            flex-grow: 1;

            .fz-hot {
                position: relative;
            }
        }
    }

    &.fz-center {
        .fz-fg {
            .fz-content {
                .fz-hot {
                    @include media-breakpoint-up(sm) {
                        max-width: 50vw;
                        margin-left: auto;
                        margin-right: auto;
                    }
                }
            }
        }
    }

    &.fz-expanded {
        .fz-fg {
            background-color: $azure;
            min-height: 50vw;

            @include media-breakpoint-up(sm) {
                min-height: 6vw;
            }

            .fz-content {
                overflow: hidden;
                margin-bottom: -$swoop-height + 0.5%;

                .fz-hot {
                    margin-bottom: 16%;
                }
            }
        }

        &.fz-center {
            .fz-fg {
                .fz-content {
                    margin-bottom: -$swoop-center-height + 0.5%;

                    .fz-hot {
                        margin-bottom: 6vw;
                    }
                }
            }
        }
    }

    &.fz-left {
        .fz-fg {
            .fz-content {
                .fz-hot {
                    @include media-breakpoint-up(sm) {
                        margin-right: $swoop-hot-margin-side;
                    }
                }
            }
        }
    }

    &.fz-right {
        .fz-fg {
            .fz-content {
                .fz-hot {
                    @include media-breakpoint-up(sm) {
                        margin-left: $swoop-hot-margin-side;
                    }
                }
            }
        }
    }

    // #### DECORATIONS ####

    $swoop-deco-sm-top: -40vw;
    $swoop-deco-sm-side: -40vw;

    .fz-deco {
        position: absolute;
        width: 120%;
        top: $swoop-deco-sm-top;

        @include media-breakpoint-up(sm) {
            position: absolute;
            width: 42%;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    &.fz-left {
        .fz-deco {
            right: $swoop-deco-sm-side;
            @include media-breakpoint-up(sm) {
                right: 0;
            }
        }
    }

    &.fz-right {
        .fz-deco {
            left: $swoop-deco-sm-side;
            @include media-breakpoint-up(sm) {
                left: 0;
            }
        }
    }

    // #### COLORS ####

    .text-strip-heading {
        color: $primary;
    }

    .text-strip, .text-strip-heading {
        background-color: rgba($body-bg, 0.92);
    }

    &.fz-expanded {
        .text-strip, .text-strip-heading {
            background-color: rgba($azure, 0.92);
        }
    }

    &.fz-dark {
        .fz-fg {
            background-color: $primary;
            color: $white;
        }

        .text-strip, .text-strip-heading {
            color: $white;
            background-color: $primary;
        }
    }

    // #### HEADER ####

    .fz-head-text {
        max-width: 30rem;
        padding: 1rem;
        margin: 30% auto 0;

        @include media-breakpoint-up(sm) {
            margin: 10% auto;
        }
    }

    &.fz-expanded {
        .fz-head-text {
            margin: 30% auto -10%;

            @include media-breakpoint-up(sm) {
                margin: 10% auto -10%;
            }
        }

        &.fz-center {
            .fz-head-text {
                margin: 10% auto 0;
            }
        }
    }

    .fz-head-photo {
        max-width: 30rem;
        margin: 0 auto -10%;
        padding: 1rem;

        @include media-breakpoint-up(md) {
            margin: 10% auto -10%;

            .fz-photo {
                position: absolute;
                width: 60%;
                height: auto;
                top: 0;
            }
        }
    }

    @include media-breakpoint-up(md) {

        &.fz-left {
            .fz-photo {
                left: 90%;

                + .fz-text {
                    padding-right: 10%;
                }
            }
        }

        &.fz-right {
            .fz-photo {
                right: 90%;

                + .fz-text {
                    padding-left: 10%;
                }
            }
        }

    }

}

.fz-mini {

    overflow: hidden;

    .fz-card {
        max-width: 20rem;
    }
}
