// Adapted from style definitions for .navbar-dark and .navbar-light to achieve contrast with primary background color
.navbar-primary {
    background-color: $primary;

    .navbar-brand {
        color: color-yiq($primary, $navbar-light-brand-color, $navbar-dark-brand-color);

        @include hover-focus {
            color: color-yiq($primary, $navbar-light-brand-hover-color, $navbar-dark-brand-hover-color);
        }
    }

    .nav-link {
        color: color-yiq($primary, $navbar-light-color, $navbar-dark-color);

        @include hover-focus {
            color: color-yiq($primary, $navbar-light-hover-color, $navbar-dark-hover-color);
        }

        &.disabled {
            color: color-yiq($primary, $navbar-light-disabled-color, $navbar-dark-disabled-color);
        }
    }

    .show > .nav-link,
    .active > .nav-link,
    .nav-link.show,
    .nav-link.active {
        color: color-yiq($primary, $navbar-light-active-color, $navbar-dark-active-color);
    }

    .navbar-toggler {
        color: color-yiq($primary, $navbar-light-color, $navbar-dark-color);
        border-color: color-yiq($primary, $navbar-light-toggler-border-color, $navbar-dark-toggler-border-color);

        .navbar-toggler-icon {
            background-image: str-replace(url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='#{color-yiq($primary, $navbar-light-color, $navbar-dark-color)}' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e"), "#", "%23");
        }

        &[aria-expanded="true"] {
            .navbar-toggler-icon {
                background-image: str-replace(url("data:image/svg+xml,%3csvg viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='#{color-yiq($primary, $navbar-light-color, $navbar-dark-color)}' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M3 3l18 18M3 21L21 3'/%3e%3c/svg%3e"), "#", "%23");
            }
        }
    }

    .navbar-text {
        color: color-yiq($primary, $navbar-light-color, $navbar-dark-color);

        a {
            color: color-yiq($primary, $navbar-light-active-color, $navbar-dark-active-color);

            @include hover-focus {
                color: color-yiq($primary, $navbar-light-active-color, $navbar-dark-active-color);
            }
        }
    }
}
