/*
Styles for the Marketo forms
 */
.mktoForm {

    @extend .p-3, .w-auto;

    display: block;
    text-align: center;
    padding-right: 10px;

    * {
        font-size: 1rem !important;
    }

    .mktoFormRow {

        display: flex;
        justify-content: center;

        .mktoField {
            clear: none !important;
        }
    }

    .mktoFieldWrap {
        @extend .form-group;
    }

    .mktoField {
        @extend .form-control, .w-auto;
    }

    .mktoCheckboxList {
        @extend .mr-2;
    }

    .mktoAsterix {
        @extend .text-danger;
    }

    button.mktoButton {
        @extend .btn, .btn-primary;

        border: 1px $primary solid !important;
        width: auto !important;
        font-weight: 400 !important;
        border-radius: 1.5rem !important;
        padding: 0.688rem 3.72rem !important;
        background-image: -webkit-gradient(linear, left top, left bottom, from($primary), to($primary)) !important;
        background-image: -webkit-linear-gradient(top, $primary, $primary !important);
        background-image: -moz-linear-gradient(top, $primary, $primary !important);
        background-image: linear-gradient(to bottom, $primary, $primary) !important;

        &:hover {
            background-color: #00497c;
            border-color: #00426f;
            background-image: -webkit-gradient(linear, left top, left bottom, from(#00497c), to(#00497c)) !important;
            background-image: -webkit-linear-gradient(top, #00497c, #00497c !important);
            background-image: -moz-linear-gradient(top, #00497c, #00497c !important);
            background-image: linear-gradient(to bottom, #00497c, #00497c) !important;
        }
    }

    .mktoButtonRow {
        display: block;
        text-align: center;
    }

    .mktoButtonWrap {
        margin-left: 0 !important;
        padding-left: 20px;
    }
}

.mktoFooter {

    .mktoForm {

        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-left: 0 !important;

        .mktoOffset {
            display: none !important;
        }

        .mktoLabel {
            display: none;
        }

        .mktoGutter {
            display: none;
        }

        .mktoField {
            width: 273px !important;
        }

        .mktoButtonWrap {
            padding-left: 0 !important;
        }
    }
}
