/*
Bootstrap Theme Configuration
For more details, see also:
https://getbootstrap.com/docs/4.0/getting-started/theming/
 */

@import url('https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,400i,700,700i,900,900i&display=swap');

$font-family-sans-serif: 'Messina', 'Montserrat', Arial, sans-serif;

$font-family-book: 'MessinaBook';

// Radian Colors

$radian-lt-gray-020: #f4f4f4;
$radian-lt-gray-040: #e9e9e9;
$radian-lt-gray-060: #dddfdd;
$radian-dk-gray-020: #d8d7d7;
$radian-lt-gray-080: #d2d4d2;
$radian-lt-gray-100: #C7C9C7;
$radian-dk-gray-040: #b1b0ae;
$radian-dk-gray-060: #8b8886;
$radian-dk-gray-080: #64615e;
$radian-dk-gray-100: #3D3935;
$radian-navy-100: #002B49;
$radian-coral-100: #FA7268;
$radian-burgundy-100: #820933;
$radian-orange-100: #EF442E;
$radian-topaz-100: #FFC882;
$radian-green-100: #065143;
$radian-teal-100: #00BAB3; // Radian Teal
$radian-teal-020: #CDF1EF; // Radian Teal at 20% opacity blended onto white
$radian-teal-010: #f2fbfb;
$support-red: #E0182D;
$support-green: #468000;
$support-yellow: #EFC100;
$support-blue: #5AAAFA;
$radian-teal-dark: #00807b;

// Bootstrap Color Palette

$white: #fff;
$gray-100: $radian-lt-gray-020;
$gray-200: $radian-lt-gray-040;
$gray-300: $radian-dk-gray-020;
$gray-400: $radian-lt-gray-080;
$gray-500: $radian-lt-gray-100;
$gray-600: $radian-dk-gray-040;
$gray-700: $radian-dk-gray-060;
$gray-800: $radian-dk-gray-080;
$gray-900: $radian-dk-gray-100;
$black: #000;
$blue: $radian-navy-100;
$indigo: #5133bd;
$purple: #562580;
$pink: $radian-coral-100;
$red: $radian-burgundy-100;
$orange: $radian-orange-100;
$yellow: $radian-topaz-100;
$green: $radian-green-100;
$teal: $radian-teal-100;
$cyan: $radian-teal-020;
$azure: $radian-teal-010;

// Color Usage

$primary: $blue;
$secondary: $teal;
$success: $support-green;
$info: $support-blue;
$warning: $support-yellow;
$danger: $support-red;
$light: $gray-100;
$dark: $gray-900;
$theme-color-interval: 6%;

// Options

$enable-shadows: true;

// Body

$body-bg: $white;
$body-color: $black;

// Links

$link-color: $radian-teal-dark;
$link-hover-color: $link-color;

// Typography

$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-semibold: 600;
$font-weight-bold: 700;
$font-weight-black: 900; // custom
$text-muted: $gray-800;
$headings-font-weight: $font-weight-bold;
$display1-weight: $font-weight-black;
$display2-weight: $font-weight-black;
$display3-weight: $font-weight-black;
$display4-weight: $font-weight-black;
$hr-border-color: $gray-600;
$hr-border-width: 1px;



// Components

$border-width: 2px;
$border-radius: .5rem;
$border-radius-lg: .75rem;
$border-radius-sm: .35rem;

// Buttons

$btn-border-radius: 1.5rem;
$btn-border-radius-lg: 1.75rem;
$btn-border-radius-sm: 1.25rem;
$btn-padding-x-lg: 5rem;
$btn-disabled-opacity: .5;

// Forms

$custom-control-indicator-border-color: $gray-800;
$custom-checkbox-indicator-border-radius: 0;
$custom-control-indicator-border-width: $border-width;

// Inputs

$input-border-color: $gray-700;
$input-border-color-light: $gray-500;
$input-bg: rgba(0, 0, 0, 0);
$input-disabled-bg: $body-bg;
$input-border-width: 1px;
$input-box-shadow: none;
$input-color: $black;
$input-border-radius: 0;
$input-border-radius-lg: 0;
$input-border-radius-sm: 0;
$input-underline: true;
$input-btn-padding-y: .688rem;
$input-btn-padding-x: 3.72rem;
$input-btn-padding-y-sm: 0.5rem;
$input-btn-padding-x-sm: 1.25rem;
$input-btn-font-size-sm: 1rem;
$input-padding-y: 0rem;
$input-padding-x: 0rem;
$input-padding-y-sm: 0rem;
$input-padding-x-sm: 0rem;
$input-padding-y-lg: 0rem;
$input-padding-x-lg: 0rem;
$input-group-padding-y: 0rem;
$input-group-padding-x: 0.5rem;
$input-group-padding-y-sm: 0rem;
$input-group-padding-x-sm: 0.5rem;
$input-group-padding-y-lg: 0rem;
$input-group-padding-x-lg: 0.5rem;
$input-group-addon-bg: $gray-200;
$input-group-addon-border-color: none;
$input-focus-border-color: $secondary;
$input-focus-box-shadow: none;
$input-transition: none;

// Tables

$table-cell-padding: 1.5rem;

// Custom Selects

$custom-select-border-radius: 0;
$custom-select-box-shadow: none;
$custom-select-disabled-bg: $azure;
$custom-select-disabled-color: $input-color;

// Dropdowns

$dropdown-btn-border-radius: 0;
$dropdown-btn-padding-y: 0;
$dropdown-btn-padding-x: 0;
$dropdown-btn-padding-y-sm: 0;
$dropdown-btn-padding-x-sm: 0;
$dropdown-btn-padding-y-lg: 0;
$dropdown-btn-padding-x-lg: 0;
$dropdown-btn-underline: true;
$dropdown-border-radius: 1rem;
$dropdown-border-width: 0;
$dropdown-box-shadow: 0 .5rem 1rem rgba($black, .175);

// Cards

$card-spacer-y: 0.75rem;
$card-spacer-x: 1.5rem;
$card-border-radius: 0.75rem;
$card-inner-border-radius: $card-border-radius;
$card-cap-bg: none;

// Pagination

$pagination-padding-y: .5rem;
$pagination-padding-x: .5rem;
$pagination-line-height: .4;
$pagination-color: $primary;
$pagination-border-color: transparent;
$pagination-hover-color: $primary;
$pagination-hover-bg: transparent;
$pagination-hover-border-color: $secondary;

// Tooltips

$tooltip-color: $body-color;
$tooltip-bg: $light;
$tooltip-border-radius: 1rem;
$tooltip-padding-x: 1rem;
$tooltip-margin: 0.25rem;

// Badges

$badge-font-size: 80%;
$badge-padding-y: .5em;
$badge-padding-x: .75em;
$badge-pill-padding-x: 1em;

// Modals

$modal-header-border-width: 0;
$modal-footer-border-width: 0;

// Lists

$list-group-bg: transparent;

// Code

$code-color: $red;



@import './common';
