.selector-indicator {
    display: inline-block;
    background-color: $white;
    width: 6rem;
    height: 6rem;
    padding: 0.5rem;
    border-radius: 50%;
    overflow: hidden;
    @include box-shadow(0 0 0.75rem rgba($black, .175));
}

.selector-card-group {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.selector-card {
    display: inline-flex;
    min-width: 15rem;
    max-width: 45rem;
    margin: 0 auto;

    .card-img-top {
        height: 5rem;

        & + .card-body {
            margin-top: -3rem;
        }

    }

}


.selector-card-container {

    display: flex;
    position: relative;
    padding: 0.5rem;
    flex-basis: 16rem; // older browsers (IE) need explicit flex-basis in order to wrap elements properly

    @supports (display: flex) {
        // modern browsers (not IE) can apply flex wrap with implicit flex-basis
        flex-basis: 0;
    }

    .checkbox-ghost {
        position: absolute;
        top: 1rem;
        left: 50%;
    }

    .selector-card {
        position: relative;
        transition: border-color 0.4s ease;

        .checkbox-label {
            position: absolute;
            top: 1rem;
            right: 1rem;

            .sr-only {
                background-color: $primary;
                color: $white;
            }

        }

        @include media-breakpoint-down(sm) {
            .card-text {
                overflow: hidden;
                max-height: 0;
                opacity: 0;
                transition: max-height 0.3s ease, opacity 0.2s ease;
            }
        }

    }

    .checkbox-ghost:checked + .selector-card {
        border-color: $primary;

        @include media-breakpoint-down(sm) {
            .card-text {
                max-height: 15rem;
                opacity: 1;
            }
        }

    }

}


