.site-header {
    $site-header-height: 5rem;

    border-bottom: 1px solid $light;
    box-sizing: content-box;
    height: $site-header-height;

    // For sticky site header on browsers lacking "sticky" support (that is, IE) set top margin on subsequent sibling element to avoid overlap
    &.sticky-top ~ * {
        margin-top: $site-header-height;
        @supports (position: sticky) {
            margin-top: 0;
        }
    }

    .site-header-row {
        display: flex;
        height: 100%;
        justify-content: space-between;

        > .site-header-col {
            display: flex;
            align-items: center;
            justify-content: flex-end;

            @include media-breakpoint-up(lg) {
                &:not(:first-child) {
                    flex-basis: 50%;
                }
            }

            > .site-header-btn-container {
                align-self: stretch;
                display: flex;
                margin: 2px 0;
                position: relative;
                z-index: 2;

                > .site-header-btn {
                    align-self: stretch;
                    z-index: 1;
                }
            }

            .site-header-btn {
                line-height: 0;
                position: relative;

                &:focus, &:hover {
                    box-shadow: none;
                    background-color: $light;
                }

                > .toggle {
                    display: inline-block;
                    position: relative;
                    width: 1em;
                    height: 1em;

                    > .toggle__opener, > .toggle__closer {
                        position: absolute;
                        left: 0;
                        right: 0;
                        width: 100%;
                        top: 0;
                        bottom: 0;
                        height: 100%;
                        transition: transform 0.3s ease;
                    }

                    > .toggle__opener {
                        transform: scale(0, 0);
                    }

                    > .toggle__closer {
                        transform: scale(1, 1);
                    }
                }

                &.collapsed {
                    > .toggle > .toggle__opener {
                        transform: scale(1, 1);
                    }

                    > .toggle > .toggle__closer {
                        transform: scale(0, 0);
                    }

                    + .caret {
                        transform: translateY(-100%);
                    }
                }

                + .caret {
                    line-height: 0;
                    position: absolute;
                    left: 0;
                    right: 0;
                    width: 100%;
                    top: 100%;
                    transform: translateY(0);
                    transition: transform 0.3s ease;

                    > .icon {
                        width: 100%;
                    }
                }
            }

        }
    }

    .site-logo {
        height: 2rem;
    }

    .btn-lg {
        padding: 0.5rem 0.25rem;
        font-size: 1.75rem;
    }

    .nav-item {

        .nav-link {
            font-size: 0.875rem;
            padding: 0 1rem;
            text-decoration: none;

            &:focus, &:hover {
                text-decoration: underline;
            }
        }
    }

    .site-menu {
        &:focus {
            outline: none;
        }

        .site-menu-content {
            height: calc(100vh - #{$site-header-height});
            overflow: auto;
        }
    }
}
