.mega {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: visible;
    overflow-y: auto;
    height: 100%;
    transform: translateZ(0); // gpu acceleration

    $d: 0.3s;
    $e: ease-out;

    .mega-secondary {
        flex-basis: 0;
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        min-width: 0;
        height: 200%;

        @for $n from 1 through 6 {
            &:nth-child(#{$n * 2 - 1}) {
                background-color: theme-color-level('primary', 1-$n);
            }
        }

        .mega-secondary-option {
            transition: opacity $d $e, visibility $d $e;

            &.mega-secondary-option-selected {
                background-color: theme-color-level('primary', -1);
            }
        }
    }

    .mega-tertiary {
        background-color: theme-color-level('primary', -1);
        flex-basis: 0;
        flex-grow: 0;
        opacity: 0;
        visibility: hidden;
        overflow-x: hidden;
        overflow-y: auto;
        width: 0;
        height: 200%;
    }
    // transitional styles
    .mega-secondary {
        // state: normal
        // transition: from inactive
        flex-grow: 1;
        opacity: 1;
        visibility: visible;
        width: 100%;
        // expand then fade
        transition: flex-grow $d $e $d, width $d*5 $e $d, opacity $d $e $d*2, visibility $d $e $d*2;
    }

    .mega-secondary.active {
        // state: normal
        // transition: from active
        flex-grow: 1;
        opacity: 1;
        visibility: visible;
        width: 100%;

        & + .mega-tertiary {
            transition: opacity $d $e, visibility $d $e, flex-grow $d $e $d, width $d $e $d;
        }
    }

    &.active .mega-secondary {
        // state: inactive
        // transition: from normal
        flex-grow: 0;
        opacity: 0;
        visibility: hidden;
        width: 0;
        // fade then contract
        transition: opacity $d $e, visibility $d $e, flex-grow $d $e $d, width 0s $e $d;
    }

    &.active .mega-secondary.active {
        background-color: $primary;
        // state: active
        // transition: from normal
        flex-grow: 1;
        opacity: 1;
        visibility: visible;
        width: 100%;

        & + .mega-tertiary {
            flex-grow: 3;
            opacity: 1;
            visibility: visible;
            width: 100%;
            transition: flex-grow $d $e $d, width $d $e $d, opacity $d $e $d*2, visibility $d $e $d*2;
        }

        .mega-secondary-option {
            opacity: 0;
            visibility: hidden;

            &.mega-secondary-option-selected {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    // Rule styles
    .mega-rule {
        margin: 0.5rem 0;
        width: 65%;
        border-color: currentColor;
        border-style: solid;
        border-bottom-width: 1px;
        border-top-width: 1px;
        height: 0;
        position: relative;

        .icon {
            position: absolute;
            left: 100%;
            top: 50%;
            transform: translateY(-50%);
        }

        @include media-breakpoint-up(lg) {
            margin: 1rem 0;
        }
    }
}

// customize buttons and links

.mega {

    .btn.btn-link {
        text-align: left;
        padding: 0.5rem 0.5rem 0.5rem 2rem;
        box-shadow: none;

        @include media-breakpoint-up(lg) {
            padding: 1rem 0.5rem 1rem 2rem;
        }

        &.btn-block {
            display: flex;
            align-items: center;

            .icon {
                color: $secondary;
                font-size: 1.25rem;
            }
        }


        &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active {
            box-shadow: none;
        }
    }
}

.bg-primary .mega {
    a:not(.btn), .btn-link {
        color: $white;
    }
}
