.card {

    .card-img, .card-img-top, .card-img-bottom {
        margin: -$card-border-width;
        width: calc(100% + #{$card-border-width * 2});
        height: 13rem;

        @supports (display: block) {
            //Support for Card Height in Non-IE Browsers
            height: auto;
        }
    }

    .card-indicator {
        height: 10rem;
        width: auto;
    }

}

.card-module {

    .card {

        max-width: 20rem;
        width: 100%; //making IE happy :(

        @include media-breakpoint-up(md) {
            max-width: 33vw;
        }

        @include media-breakpoint-up(lg) {
            max-width: 20rem;
        }
    }

    @supports (display:block) {
        justify-content: center;
    }
}

// set breakpoint where card-deck switches from vertical to horizontal layout

.card-deck {

    @supports (display: block) {

        flex-flow: column nowrap;

        @include media-breakpoint-up(md) {
            flex-flow: row wrap;
        }
    }
}
