@if variable-exists('font-weight-black') {
    .font-weight-black {
        font-weight: $font-weight-black !important;
    }
}

@if variable-exists('font-weight-semibold') {
    .font-weight-semibold {
        font-weight: $font-weight-semibold !important;
    }
}

@if variable-exists('font-family-book') {
  .font-family-book {
    font-family: 'MessinaBook' !important;
  }
}

.bg-primary {
    a:not(.btn) {
        color: $secondary;
    }
}

a {
    font-weight: $font-weight-bold;

    &.list-group-item, &.nav-link {
        font-weight: $font-weight-normal;
    }

    &:focus {
        outline-color: rgba($teal, .75);
        outline-style: solid;
        outline-width: $btn-focus-width;
    }
}
