/* styles for indicator graphics */

$indicator-size-sm: 4rem;
$indicator-size-md: 6rem;
$indicator-size-lg: 8rem;

.indicator {
    width: $indicator-size-md;
    height: $indicator-size-md;
}

.indicator-sm {
    width: $indicator-size-sm;
    height: $indicator-size-sm;
}

.indicator-lg {
    width: $indicator-size-lg;
    height: $indicator-size-lg;
}
