.nav-tabs {

    $caret-height: 24px;
    $caret-inset: 6px;

    background-color: $azure;
    flex-wrap: nowrap;

    .nav-item {

        flex: 1 1 0;
        margin: 0 0.5rem;
        min-width: 10rem;
        max-width: 14rem;
        padding-bottom: $caret-height - $caret-inset;
        position: relative;

        &:first-child {
            margin-left: 1rem;
        }

        &:last-child {
            margin-right: 1rem;
        }

        .nav-link {
            background-color: $white;
            border: 0;
            border-bottom: $border-width solid $secondary;
            color: $primary;

            .caret {
                display: inline-block;
                line-height: 0;
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                visibility: hidden;

                .icon {
                    color: $primary;
                    font-size: $caret-height;
                }
            }

            &.active {
                background-color: $primary;
                border-color: $primary;
                color: $white;

                .caret {
                    visibility: visible;
                }
            }

        }
    }

}
