.btn-primary {
    @include button-variant (
        $background: $primary,
        $border: $primary,
        $hover-background: lighten($primary, 10%),
        $hover-border: lighten($primary, 7.5%),
        $active-background: lighten($primary, 5%),
        $active-border: lighten($primary, 5%)
    );

    &:focus {
        box-shadow: 0 0 0 $btn-focus-width rgba($teal, .75);
    }
}

.btn-outline-primary {
    @include button-outline-variant(
        $color: $primary,
        $color-hover: $primary,
        $active-background: $light,
        $active-border: $primary
    );

    background-color: $white;

    &:hover, &:focus {
        background-color: $gray-200;
    }

    &:focus {
        box-shadow: 0 0 0 $btn-focus-width rgba($teal, .75);
    }

}
