.dropdown,
.dropup,
.dropleft,
.dropright {

    .btn-outline-primary,
    .btn-outline-secondary,
    .btn-outline-success,
    .btn-outline-danger,
    .btn-outline-warning,
    .btn-outline-info,
    .btn-outline-light,
    .btn-outline-dark {

        border-radius: $dropdown-btn-border-radius;
        border-width: $input-border-width;
        padding: $dropdown-btn-padding-y $dropdown-btn-padding-x;

        @if $dropdown-btn-underline {
            border-style: none none solid;
        }
    }

}

.menu-short {
    height: 30vh;
}
