/* Styles for completely styled checkbox input */

.checkbox-ghost {
    width: 0;
    height: 0;
    opacity: 0;

    & + * .checkbox-label {
        $checkbox-label-size: 2rem;
        $checkbox-label-spacing: 0.25rem;

        cursor: pointer;
        font-size: $checkbox-label-size;
        line-height: 0;
        padding: $checkbox-label-spacing;
        margin: -$checkbox-label-spacing;
        border-radius: 50%;
        border-width: $border-width;
        border-color: rgba(0, 0, 0, 0);
        border-style: solid;
        transition: border-color 0.1s ease 0.1s; // prevent flickering when user clicks when already focused

        svg.icon {
            border-radius: 50%;
        }

        .checkbox-label--on {
            display: none;
            background-color: $white;
            color: $secondary;
        }

        .checkbox-label--off {
            display: inline-block;
            background-color: $white;
            color: $light;
        }
    }

    &:focus + * .checkbox-label {
        border-color: $secondary;
    }

    &:checked + * .checkbox-label {
        .checkbox-label--on {
            display: inline-block;
        }

        .checkbox-label--off {
            display: none;
        }
    }
}
