.state-map {

    width: 100%;
    max-width: 768px;
    height: auto;

    .option {
        fill: $gray-500;

        &.active {
            fill: $primary;
        }

        &:focus, &:hover {
            stroke: $secondary;
            stroke-width: 2px;
        }
    }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
    /* IE10+ specific styles go here */  
    .state-map {
      height: 593px;
    }
 }
