.validation-summary {
    margin: auto;
    text-align: center;
    color: $danger;
    font-size: .9rem;

    ul {
        list-style: none;
        padding: 0;

        li {
            margin-bottom: .75rem;
        }
    }
}

.email-verification {

    .validation-summary-errors {
        @extend .validation-summary;
        max-width: 30rem;
    }

}

.create-profile {

    .validation-summary-errors {
        @extend .validation-summary;
        max-width: 45rem;
    }
}


.field-validation-error {
    color: $danger;
}

input.field-validation-error {
    color: $black;
}

.form-control.dynamic.input-validation-error {
    border-color: $danger;
}
