.leaflet {

    display: flex;
    flex-wrap: nowrap;
    overflow-x: visible;
    position: relative;
    left: 0;
    transition: left 0.2s ease-out;

    &.leaflet-open {
        left: -100%;
    }

    .leaflet-page {
        flex-basis: 100%;
        flex-grow: 1;
        flex-shrink: 0;
    }

    .leaflet-rule {
        margin: 1rem 0;
        width: 0;
        animation: widen-rule;
        animation-duration: 0.4s;
        animation-delay: 0.2s;
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;
        border-color: currentColor;
        border-style: solid;
        border-bottom-width: 1px;
        border-top-width: 1px;
        height: 0;
        position: relative;

        .icon {
            position: absolute;
            left: 100%;
            top: 50%;
            transform: translateY(-50%);
        }

        @keyframes widen-rule {
            to {
                width: 65%;
            }
        }

    }

}

// customize buttons and links

.leaflet {

    .btn.btn-link {

        text-align: left;
        padding: 1rem 1.5rem 1rem 2rem;

        &.btn-block {
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
        }

        &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active {
            box-shadow: none;
        }
    }

}

.bg-primary .leaflet {
    a:not(.btn), .btn-link {
        color: $white;
    }
}
